import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Login } from "src/app/model/login";
@Injectable({
  providedIn: "root"
})
export class LoginService {
  constructor(private http: HttpClient) {}

  public AuthenUser(login) {
    return this.http.post<any>("/api/login/", login).subscribe(res => {
      if (res.success) {
        localStorage.setItem("cookie_login", JSON.stringify(res.data));
      } else {
        localStorage.removeItem("cookie_login");
      }

    });
  }
}
