import { Component, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import Swal from "sweetalert2";
import { Users } from "src/app/Model/User";

declare var $;
@Component({
  selector: "app-employeecreate",
  templateUrl: "./employeecreate.component.html",
  styleUrls: ["./employeecreate.component.css"],
})
export class EmployeecreateComponent implements OnInit {
  usermodel: any = {};
  constructor(private http: HttpClient) {}
  users_: Users;
  //usermodel : User;
  fileImg_: File;
  imagePath;
  imgURL1: any;
  message: string;
  role_ = [];
  fileData = null;
  ngOnInit() {
    this.role_ = this.getRoles();
    // $(() => {
    //   $(".chosen-select").chosen({});
    // });
  }
  preview(files) {
    if (files.length === 0) return;
    this.fileData = files[0];

    var mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      this.message = "Only images are supported.";
      return;
    }

    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.imgURL1 = reader.result;
    };
  }

  fileUpload() {
    const formData = new FormData();
    formData.append("file", this.fileData);
    this.http.post<any>("/api/fileupload", formData).subscribe((res) => {
      console.log(res);
    });
  }

  onSubmit() {
    this.usermodel.imgurl = "";
    this.users_ = this.usermodel;
    console.log("user data: ", this.users_);

    this.http.post<any>("/api/user", this.usermodel).subscribe((res) => {
      //alert(JSON.stringify(res));
      //this.msgs = res.message;
      if (res.success) {
        Swal.fire({
          // position: "top-end",
          icon: "success",
          title: res.message,
          showConfirmButton: false,
          timer: 1500,
        });
      } else {
        Swal.fire({
          //position: "top-end",
          icon: "error",
          title: res.message,
          showConfirmButton: false,
          timer: 1500,
        });
      }
    });
  }
  getRoles() {
    return [
      { id: "0", name: "--Select Role--" },
      { id: "1", name: "Admin" },
      { id: "2", name: "User" },
    ];
  }
}
