import { Component, OnInit } from "@angular/core";
import { EmployeeService } from "src/app/services/employee.service";

import { UsersService } from "src/app/services/users.service";
import { Users } from "src/app/Model/User";
import { ActivatedRoute, Router } from "@angular/router";

@Component({
  selector: "app-users",
  templateUrl: "./users.component.html",
  styleUrls: ["./users.component.css"],
})
export class UsersComponent implements OnInit {
  p: number = 1;
  pagelimit = 15;
  users: Users[];
  config: any;

  constructor(
    private apiUser: UsersService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.config = {
      currentPage: 1,
      itemsPerPage: 15,
      totalItems: 0,
    };
    route.queryParams.subscribe(
      (params) =>
        (this.config.currentPage = params["page"] ? params["page"] : 1)
    );
    console.log("api loading .....");
  }

  ngOnInit() {
    this.getUserList();
  }
  getUserList() {
    this.apiUser.getUsers().subscribe((res) => {
      this.users = res;
      console.log("api data :", this.users);
    });
  }
  pageChanged(newPage: number) {
    console.log("click api loading .....");
    this.router.navigate(["users/list"], { queryParams: { page: newPage } });
  }
  limitpage() {
    //this.pagelimit = id;
    this.config = {
      currentPage: 1,
      itemsPerPage: this.pagelimit,
      totalItems: 0,
    };
    console.log("show limit page :" + this.p + "  limit :", this.config);
  }
  limits = [
    { value: "2", viewValue: "2" },
    { value: "5", viewValue: "5" },
    { value: "10", viewValue: "10" },
    { value: "15", viewValue: "15" },
    { value: "20", viewValue: "20" },
    { value: "30", viewValue: "30" },
    { value: "50", viewValue: "50" },
  ];
}
