import { Component, OnInit } from "@angular/core";
import { AuthService, FacebookLoginProvider } from "angularx-social-login";
import { Router } from "@angular/router";
import Swal from "sweetalert2";
@Component({
  selector: "app-sitemenu",
  templateUrl: "./sitemenu.component.html",
  styleUrls: ["./sitemenu.component.css"]
})
export class SitemenuComponent implements OnInit {
  constructor(private _socioAuthServ: AuthService, private router: Router) {}

  ngOnInit() {}

  signOut(): void {
    Swal.fire({
      title: "Confirm",
      text: "Are you sure to logout system?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, logout"
    }).then(result => {
      if (result.value) {
        //Swal.fire("Deleted!", "Your file has been deleted.", "success");

        this._socioAuthServ.signOut();
        localStorage.removeItem("SocialLogin");
        console.log("User signed out.");
        this.router.navigateByUrl("/login");
      }
    });
  }
}
