import { Component, OnInit, ViewChild } from "@angular/core";
import { PopupconfirmComponent } from "../popupconfirm/popupconfirm.component";
import Swal from "sweetalert2";
import { EmployeeService } from "src/app/services/employee.service";

declare var $;

@Component({
  selector: "app-setting",
  templateUrl: "./setting.component.html",
  styleUrls: ["./setting.component.css"]
})
export class SettingComponent implements OnInit {
  constructor(private ApiEmp: EmployeeService) {}

  openModal: boolean = false;
  msgTitle: string = "Message alert !";
  msgDetail: string = "ABCDEFGHIJKLMNOPQRSTUVWXYZ !";

  @ViewChild("popupconfirm", { static: false })
  modal: PopupconfirmComponent;

  popup(): void {
    this.modal.ShowModal("Confirm", "Are you logout?");
  }
  open(): void {
    $("#myModal222").modal("show");
  }
  onClickConfirm() {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!"
    }).then(result => {
      if (result.value) {
        Swal.fire("Deleted!", "Your file has been deleted.", "success");
      }
    });
  }

  ngOnInit(): void {
    this.ApiEmp.getEmployee().subscribe(res => {
      console.log("api data :", res);
    });

    console.log(
      "SocialLogin :",
      JSON.parse(localStorage.getItem("SocialLogin"))
    );

    $(() => {
      $(".datepicker1").datetimepicker({
        format: "DD/MM/YYYY"
      });
    });

    $(() => {
      $(".chosen-select1").chosen({});
    });
  }
}
