import { Component, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
declare var $: any;
@Component({
  selector: "app-covid19",
  templateUrl: "./covid19.component.html",
  styleUrls: ["./covid19.component.css"]
})
export class Covid19Component implements OnInit {
  constructor(private httpClient: HttpClient) {}
  urlapi =
    "https://covid19-cdn.workpointnews.com/api/constants.json?fbclid=IwAR0cc_mjdtmcoe3ZC4vK9mh41ugnUpoLUQLDhF7_ejPv-1kCP0sFiCXsRnU";

  peples;

  a1;
  a2;
  a3;
  a4;
  a5;
  a6;
  a7;
  a8;
  today: number;

  ngOnInit() {
    this.getApi();
  }

  getApi() {
    this.httpClient.get<Covid19>(this.urlapi).subscribe(res => {
      this.peples = JSON.parse(JSON.stringify(res));
      this.a1 = this.peples.โน๊ตผู้เสียชีวิต;
      this.a2 = this.peples.หายแล้ว;
      this.a3 = this.peples.เสียชีวิต;
      this.a4 = this.peples.เพิ่มวันนี้;
      this.a5 = this.peples.กำลังรักษา;
      this.a6 = this.peples.ผู้ติดเชื้อ;
      this.a7 = this.peples.โน๊ตผู้ติดเชื้อ;
      this.a8 = this.peples.เพิ่มวันที่;
      console.log("api :", this.peples.หายแล้ว);

    });
    this.today = Date.now();
  }
}

export interface Covid19 {
  a1: string;
  a2: string;
  a3: string;
  a4: string;
  a5: string;
  a6: string;
  a7: string;
  a8: string;
}
